<template>
    <div>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <fine-record
            v-if="currentStep === steps.findIndex(step=>step.key === 'fineRecord')"
            :payType="0"
            :isCheck="true"
            :searchFunction="searchFunction"
            :list="illegalityPayList"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import fineRecord from './components/fine-record';
import { illegalityPayQueryList } from '@/service/traffic/traffic';
import audioCom from '@/assets/media/trafic/11.mp3';
import { ILLEGALITY_PAYMENT_CHECK } from '@/constant/stepsOption';

export default {
    name: 'illegality-pay-check',
    components: {
        fineRecord,
        identifyCardPage
    },
    data () {
        return {
            audioCom: audioCom,
            inputData: '',
            illegalityPayList: []
        };
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep'
        })
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        ...mapMutations('illegality', ['setIllegalityPayIntroduce', 'setIllegalityPayResult']),
        async searchFunction (data, inputData) {
            this.inputData = inputData;
            const res = await illegalityPayQueryList(data);
            if (res && res.code === '0') {
                this.$message.success('查询成功！');
                const result = res.result;

                result.forEach((item) => {
                    if (!item.lateFee) {
                        item.lateFee = 0;
                    }
                    if (!item.payAmount) {
                        item.payAmount = 0;
                    }
                });
                this.illegalityPayList = result;

                if (res.result.length === 0) {
                    this.changeTipPage({ message: '未查询到待缴费记录！', type: 1, enableBack: true });
                }
            } else if (res) {
                this.changeTipPage({ message: res.message, enableBack: true });
            }
        }
    },
    mounted () {
        this.setSteps(ILLEGALITY_PAYMENT_CHECK);
    }
};

</script>
