<template>
    <div>
        <div class="top-text">
            <kd-full-input
                :ime-type="3"
                class="decisionInput"
                :watch-data="getDecision"
                :placeholder="'请输入'+ typeText"
                ref="decisionInput"
            />
            <kd-button
                class="searchBut"
                text="搜索"
                :button-handle="searchInfo"
                :but-disable="!decisionInput"
            />
        </div>
        <div v-if="payType === 1" class="tip-text">注：若决定书编号中含有其它字符，可忽略，仅输入数字即可</div>
        <div class="error-tip" v-if="errorMsg">{{ errorMsg }}</div>

        <div class="kd-page-content-wrapper wrap-info kd-table-style" :style="{marginTop: errorMsg ? '0' : '20px'}">
            <el-table
                ref="multipleTable"
                :data="pageData"
                header-row-class-name="header-row"
                style="width: 100%"
                @select="selectionChange"
                @select-all="selectionAll"
            >
                <el-table-column
                    :prop="item.key"
                    :label="item.label"
                    :width="item.width"
                    v-for="(item,index) in illegalList" :key="index"
                    :formatter="listFormatTime"
                    :type="item.columnType"
                    show-overflow-tooltip
                />
            </el-table>
            <!-- 总费用： -->
            <div
                class="kd-total-content"
                v-if="!isCheck"
            >
                <div class="left">
                    <span @click="changePage(-1)"><i class="el-icon-caret-left"></i>上一页</span>
                    <span @click="changePage(1)">下一页<i class="el-icon-caret-right"></i></span>
                </div>
                <div class="left"></div>
                <div class="right">
                    <p>总费用：￥<span>{{ totalPay || 0 }}</span></p>
                </div>
            </div>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button icon="kedaIcon-policework" text="返回首页" type-class="home"/>
            <kd-button icon="kedaIcon-circleleft" text="上一步" :button-handle="preStep"/>
            <kd-button icon="kedaIcon-duihao2" v-if="!isCheck" text="确认缴款" :button-handle="submine" :but-disable="!(this.selection && this.selection.length > 0)"/>
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { checkIdNum } from '@/utils/tools';
import { keyboardHide } from '@/service/common';
import audioCarDes from '@/assets/media/trafic/201.mp3';
import audioCarId from '@/assets/media/trafic/202.mp3';
import { playAudio } from '@/utils/media';
import { illegalList } from '@/utils/regional';

export default {
    name: 'IllegalityPayFineRecord',
    data () {
        return {
            errorMsg: '', // 错误信息输入提示
            typeText: '决定书编号',
            selection: [],
            pageData: [],
            decisionInput: '',
            currentPage: 1,
            illegalList: illegalList(this.CONSTANT.location),
            defaultNum: 5 // 默认显示三条数据
        };
    },
    props: {
        isCheck: Boolean,
        payType: Number,
        list: { type: Array, default: () => [] },
        nextFunction: { type: Function, default: function () { this.changeStep('down'); } },
        searchFunction: { type: Function },
        getSelectionList: { type: Function }

    },
    mounted () {
        this.audioCarDes = playAudio(audioCarDes);
        this.audioCarId = playAudio(audioCarId);
        if (this.payType === 1) {
            this.audioCarDes.play();
            this.typeText = '决定书编号';
        } else {
            this.audioCarId.play();
            this.typeText = '驾驶证号（身份证号）';
            this.$refs.decisionInput.changeData(this.identifyCard.certNumber);
        }

        // this.illegalList = illegalList(this.CONSTANT.location);

        this.changePage(null, 1);
    },
    computed: {
        totalPage () {
            return Math.ceil(this.list.length / this.defaultNum) || 1;
        },
        totalPay () { // 付费总金额
            if (this.selection.length > 0) {
                let totalPrice = 0;
                this.selection.map((item) => {
                    totalPrice += item.lateFee + item.payAmount; // 滞纳金+ 罚款金额
                });
                return totalPrice;
            } else {
                return 0;
            }
        },
        ...mapState('common', {
            businessTypeCode: 'businessTypeCode',
            identifyCard: 'identifyCard'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('illegality', ['setIllegalityPayList', 'setIllegalityPayUrl', 'setIllegalityPayInfo']),
        getDecision (data) {
            this.decisionInput = data;
        },
        preStep () { // 上一步
            this.changeStep('up');
        },
        async submine () { // 确认交款
            if (this.selection) {
                await this.nextFunction();
            }
        },
        async searchInfo () { // 搜索功能
            const data = {};
            if (this.payType === 1) {
                data.decisionNumber = this.decisionInput;
            } else {
                if (checkIdNum(this.decisionInput)) {
                    this.errorMsg = '';
                    data.clsbdh = this.decisionInput;
                    data.jszh = this.decisionInput;
                } else {
                    this.errorMsg = '身份证输入有误';
                    return false;
                }
            }

            await this.searchFunction(data, this.decisionInput);
            this.changePage(null, 1);
        },
        selectionAll (selection) {
            this.selection = selection;
            this.getSelectionList(this.selection);
        },
        selectionChange (selection, row) { // 获取选择的缴费项
            const decisionNumbers = [];
            const resultArr = [];

            const cancelSelect = selection.find((item) => {
                return item.penaltyDetailId === row.penaltyDetailId;
            });

            selection.forEach((item, index) => {
                decisionNumbers.push(item.decisionNumber);
            });

            this.list.filter((item) => {
                const res = decisionNumbers.find((i) => {
                    return i === item.decisionNumber;
                });
                if (res) {
                    if (cancelSelect) { // 勾选状态
                        resultArr.push(item);
                    } else { // 取消状态
                        if (item.decisionNumber !== row.decisionNumber) {
                            resultArr.push(item);
                        }
                    }
                }
            });
            this.selection = resultArr;
            this.$refs.multipleTable.clearSelection();
            this.selection.forEach((item) => {
                this.$refs.multipleTable.toggleRowSelection(item, true);
            });
            this.getSelectionList(this.selection);
        },
        changePage (type, defaultPage) {
            let page = defaultPage || this.currentPage;
            if (type === -1) {
                page = page - 1;
                if (page < 1) {
                    return;
                }
            } else if (type === 1) {
                page = page + 1;
                if (page > this.totalPage) {
                    return;
                }
            }
            this.currentPage = page;
            this.pageData = this.list ? this.list.slice(this.defaultNum * (page - 1), this.defaultNum * (page - 1) + this.defaultNum) : [];
            setTimeout(() => {
                this.selection.forEach((row) => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            });
        },
        listFormatTime (row, column, cellValue, index) {
            const item = this.illegalList.find((i) => {
                return i.key === column.property;
            });

            if (item && item.type === 'time') {
                return this.$moment(cellValue).format('YYYY/MM/DD');
            } else {
                return cellValue || '-';
            }
        }
    },
    beforeDestroy () {
        this.audioCarDes.destroy();
        this.audioCarId.destroy();
        keyboardHide();
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        padding: 0;
        height: 45vh;
    }
    .top-text {
        height: 62px;
        text-align: center;
        font-size: 16px;
        color: #ffffff;
        display: flex;
        justify-content: center;
        margin: 80px auto 0;
    }

    .error-tip {
        color: red;
        font-size: 20px;
        margin-bottom: 5px;
        text-align: center;
        height: 25px;
    }

    .tip-text {
        text-align: center;
        color: #d1def7;
        margin: 5px 0;
        font-size: 18px;
    }

    .disable-info {
        cursor: no-drop;
        background: linear-gradient(180deg, rgba(197, 215, 234, 1) 0%, rgba(165, 191, 216, 1) 100%);
        border-image: linear-gradient(180deg, rgba(225, 235, 243, 1), rgba(214, 227, 239, 1)) 1 1;
        color: #fff;
    }

    .alertWrap {
        width: 100vw;
        height: 100vh;
        background: linear-gradient(180deg, rgba(232, 235, 244, 1) 0%, rgba(210, 212, 225, 1) 100%);
    }

    .searchBut {
        background: linear-gradient(180deg, rgba(251, 149, 39, 1) 0%, rgba(255, 115, 20, 1) 100%);
        border-radius: 8px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        height: 60px;
        width: 12vw;
    }

    .decisionInput {
        color: #606266;
        display: inline-block;
        width: 35vw;
        vertical-align: bottom;
        margin-right: 20px;
    }

    .chooseItem {
        font-size: 20px;
        color: #5FAFEB;
        text-align: center;
    }

    .noChooseItem {
        font-size: 20px;
        color: #fff;
        text-align: center;
    }
</style>
